<!--user-->
            <div v-if="JSON.stringify(this.user)!='{}'">
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-user"></i>
                    <p></p>
                </template>
                {{ user. username}}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-user"></i>
                    <p></p>
                </template>
                {{ user. createTime}}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-user"></i>
                    <p></p>
                </template>
                {{ user. lastLoginTime}}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-user"></i>
                    <p></p>
                </template>
                {{ user. name}}
            </el-descriptions-item>
            </div>

<!--teacher-->
            <div v-if="JSON.stringify(teacher)!='{}'">
            <el-descriptions-item :value-format="yyyy-MM">
                <template slot="label">
                    <i class="el-icon-user"></i>
                    
                </template>
                {{ teacher. age}}
            </el-descriptions-item>
            <el-descriptions-item :value-format="yyyy-MM">
                <template slot="label">
                    <i class="el-icon-user"></i>
                    
                </template>
                <p v-if="teacher. sex == 1"></p>
                <p v-else></p>
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-location-outline"></i>
                    email
                </template>
                {{ teacher. email}}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-mobile-phone"></i>
                    phone
                </template>
                {{ teacher. phone}}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-tickets"></i>
                    
                </template>
                <el-tag size="small">{{ teacher.teacherPost }}</el-tag>
            </el-descriptions-item>
            </div>
<!--            -->
            <div v-if="JSON.stringify(role)!='{}'">
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-tickets"></i>
                    
                </template>
                <el-tag size="small">{{ role.name }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-tickets"></i>
                    
                </template>
                <el-tag size="small">{{ role.roleDesc }}</el-tag>
            </el-descriptions-item>
            </div>
        </el-descriptions>





<!--  -->
	<el-dialog title="" :visible.sync="dialogFormVisible1">
	<div style="width:60%;margin: 0 auto">
	<el-form ref="attr" :model="attr" :inline="false" label-width="90px" class="demo-ruleForm">

	<el-form-item label="" prop="name2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
		<el-input  type="text" v-model="attr.name2" placeholder="" auto-complete="off"></el-input>
	</el-form-item>
	<el-form-item label="" prop="instituteNumber2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
		<el-input  type="text" v-model="attr.instituteNumber2" placeholder="" auto-complete="off"></el-input>
	</el-form-item>

	</el-form>
 </div>
	<div slot="footer" class="dialog-footer">
		<el-button @click="dialogFormVisible1 = false"> </el-button>
		<el-button @click="resetForm('attr')"></el-button>
		<el-button type="primary" @click="submitForm('attr')"> </el-button>
	</div>
</el-dialog>

	</el-row>


</template>

<script>
import http from '../../utils/http'
export default {
	data() {
		return {
			filters: {
				keyword1: ''
			},
			attr: {
				name2: '',
				instituteNumber2: ''
			},
			listLoading: false, // 
			pageSizes1: [30, 50, 80, 100],
			pageSizes2: [5, 10],
			startPage1: 1,
			startPage2: 1,
			pageSize1: 30,
			pageSize2: 5,
			total1: 0,
			total2: 0,
			tableData1: [],
			tableData2: [],
			total1: 0,
			total2: 0,
			page1: 1,
			page2: 1,
			dialogTableVisible1: false,
			dialogFormVisible1: false,
			dialogFormVisible2: false,
			innerVisible: false,
			attrIds: [], // ids
			detailIds: [], // ids
			attrId: '',
            //
            role:{
                id: "2",
                name: "",
                roleDesc: ""
            },
            teacher:{
                age: 1,
                email: "1743225606@qq.com",
                id: "dbca8e5caec24c6ca4b3dccd053f9102",
                name: "",
                phone: "13800138000",
                sex: 2,
                title: "003",
                teacherPost: ""
            },
            user:{
                createTime: "Thu Aug 26 06:54:04 CST 2021",
                enable: 1,
                id: "dbca8e5caec24c6ca4b3dccd053f9102",
                lastLoginTime: "Mon Jan 10 01:45:19 CST 2022",
                name: "name21",
                password: "d477887b0636e5d87f79cc25c99d7dc9",
                username: ""
            },
            student:{
                age: 22,
                clazzId: "96332ae13fd64c1abf5f6f778c873102",
                email: "2416292470@qq.com",
                id: "032e7ac5fe4d45ff824c7cf125c70d3f",
                instituteId: "271c18020e6a11ea85a85c93a27933da",
                majorId: "940c25ce0e7111ea85a85c93a27933da",
                name: "",
                phone: "12345678910",
                sex: 1,
                studentNumber: "182726103"
            }


		}
	},
	methods: {
		// 
		async getFormData1 () {
			let _this = this
			_this.listLoading = true
            let user = JSON.parse(sessionStorage.getItem('user'))
			let params = {
                id:user.id,
                roleId:user.roleId,
                roleName:user.roleName,
                username:user.username
			}
			let data = await http.get('account/center', params)

			if(!data.data) {
				_this.listLoading = false
				return
			}

			if (data.data.status === 200) {
                let res = data.data.data.map
                console.log(res)
				 _this.role = res.role
				 _this.teacher = res.teacher
                _this.user = res.user
                _this.teacher.teacherPost = res.teacherPost
                _this.student = res.student

                console.log(JSON.stringify(_this.teacher)!='{}')
			} else {
				_this.message(true,data.data.msg,'error')
				_this.formData1 = []
			}
			_this.listLoading = false
		},
		// 
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.addInstitute()
				} else {
					console.log('error submit!!');
					return false
				}
			})
		},
		// 
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		// 
		async addInstitute() {
			let _this = this
			let params = {
				name: _this.attr.name2,
				instituteNumber: _this.attr.instituteNumber2
			}
			let data = await http.post("institute/add", params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				  _this.resetForm('attr')
				  _this.dialogFormVisible1=false
          _this.message(true,data.data.msg,'success')
					_this.getFormData1()
			} else {
         _this.message(true,data.data.msg,'error')
			}
		},
		// 
		async delAttr () {
			if (this.attrIds.length === 0) {
				 this.message(true,'','warning')
				 return
			}
			this.$confirm(', ?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
			}).then(() => {
				this.delAttrs()
			}).catch(() => {
				this.message(true,'','warning')
			})
		},
		// 
		async delAttrs() {
      let _this = this
			let params = {
				ids: _this.attrIds
			}
			let data =await http.post('institute/delete', params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				 _this.message(true,data.data.msg,'success')
			} else {
				 _this.message(true,data.data.msg,'error')
			}
			_this.getFormData1()
		},
		// 
		handleSelectionChange1(val) {
			this.attrIds = []
			if (val) {
			 val.forEach(row => {
				 this.attrIds.push(row.id)
			 });
		 }
		},
		// 
		handleSizeChange1 (val) {
			this.pageSize1 = val
			this.getFormData1()
		},
		// 
		handleCurrentChange1 (val) {
			this.startPage1 = val
			this.getFormData1()
		},

		// 
		submitForm1(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.addDetail()
				} else {
					console.log('error submit!!');
					return false
				}
			})
		},
		// 
		async addDetail() {
			 let _this = this
			 let params = {
				 id: _this.attrId,
				 name: _this.detail.name
			 }
			 let data = await http.post('SysApi/v1/addAttributeDetail', params)

			 if(!data.data) {
			 	return
			 }

			 if (data.data.status === 200) {
 				  _this.resetForm('detail')
           _this.message(true,data.data.msg,'success')
 					_this.selDetails(this.attrId)
 			} else {
          _this.message(true,data.data.msg,'error')
 			}
		},
		// 
		async selDetails(id) {
			let _this = this
			_this.dialogTableVisible1 = true
			_this.attrId = id
			let params = {
				id: id,
				pageSize: _this.pageSize2,
				startPage: _this.startPage2
			}
			let data = await http.get('SysApi/v1/findAttributesDetailByPage', params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				 _this.tableData2 = data.data.data
				 _this.total2 = data.data.total
			} else {
				_this.message(true,data.data.msg,'error')
				_this.formData2 = []
			}
		},
		// 
		async delDetails() {
      let _this = this
			if (_this.detailIds.length === 0) {
				 this.message(true,'','warning')
				 return
			}

			let params = {
				ids: _this.detailIds
			}
			let data =await http.post('SysApi/v1/delAttributeDetails', params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				 _this.message(true,data.data.msg,'success')
			} else {
				 _this.message(true,data.data.msg,'error')
			}
			_this.selDetails(this.attrId)
		},
		// 
		handleSelectionChange2(val) {
			this.detailIds = []
			if (val) {
			 val.forEach(row => {
				 this.detailIds.push(row.id)
			 });
		 }
		},
		// 
		handleSizeChange2 (val) {
			this.pageSize2 = val
			this.selDetails(this.attrId)
		},
		// 
		handleCurrentChange2 (val) {
			this.startPage2 = val
			this.selDetails(this.attrId)
		},
		/**
		 * ifshow: true/false msg: message  type: error/warning/success
		 */
		message(ifshow,msg,type) {
			this.$message({
				showClose: ifshow,
				message: msg,
				type: type
			})
		}
	},
	mounted() {
		this.getFormData1()
	}
}
</script>



