	<div style="width:60%;margin: 0 auto">
	<el-form ref="attr" :model="attr" :inline="false" label-width="90px" class="demo-ruleForm">
		<el-form-item label="" prop="courseType2" :rules="[{ required: true, message: '', trigger: 'change,blur' }]">
			<el-select v-model="attr.courseType2" filterable placeholder="">
