				</el-option>
			</el-select>
		</el-form-item>
		<el-form-item label="" prop="name2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
			<el-input  type="text" v-model="attr.name2" placeholder="" auto-complete="off"></el-input>
		</el-form-item>
        <el-form-item label="" prop="name2" :rules="[{ required: true, message: '', trigger: 'blur' }]">
            <el-input  type="text" v-model="attr.studentNumber" placeholder="" auto-complete="off"></el-input>
        </el-form-item>
		<el-form-item label="" prop="credit2" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^([1-9][0-9]{0,1}|100)$/, message: '1-100', trigger: 'change,blur'}]">
			<el-input  type="number" v-model="attr.credit2" placeholder="" auto-complete="off"></el-input>
