        <!--  -->
        <el-dialog  title="" :visible.sync="dialogDetailEditvisble" width="1100px">
            <div style="width:600px;margin: 0 auto">




                <el-form ref="attr" :model="DetailEditvisbleMessage" :inline="false" label-width="170px" class="demo-ruleForm" label-position="left">
                    <el-form-item label="" prop="ccourseType" :rules="[{ required: true, message: '', trigger: 'change,blur' }]">
                        <el-select v-model="DetailEditvisbleMessage.ccourseType" filterable placeholder="">
                            <el-option
                                    v-for="item in courseTypes"
                                    :key="item.dictCode"
                                    :label="item.dictValue"
                                    :value="item.dictCode">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="sname" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                        <el-input  type="text" v-model="DetailEditvisbleMessage.sname" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="studentNumber" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                        <el-input  type="text" v-model="DetailEditvisbleMessage.studentNumber" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="sphone" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                        <el-input  type="text" v-model="DetailEditvisbleMessage.sphone" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="semail" :rules="[{ required: true, message: '', trigger: 'blur' }]">
                        <el-input  type="text" v-model="DetailEditvisbleMessage.semail" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="gcompanyName">
                        <el-input  type="text" v-model="DetailEditvisbleMessage.gcompanyName" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="gstudentsPost">
                        <el-input  type="text" v-model="DetailEditvisbleMessage.gstudentsPost" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="gcDescribe">
                        <el-input type="textarea" v-model="DetailEditvisbleMessage.gcDescribe" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="goutSupervisor">
                        <el-input  type="text" v-model="DetailEditvisbleMessage.goutSupervisor" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="supervisorPost">
                        <el-input  type="text" v-model="DetailEditvisbleMessage.supervisorPost" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="goutorPhone">
                        <el-input  type="text" v-model="DetailEditvisbleMessage.goutorPhone" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>


                    <el-form-item label="" prop="validateTime" >
