                        <el-date-picker
                                v-model="DetailEditvisbleMessage.validateTime"
                                value-format = "yyyy-MM-dd"
                                type="daterange"
                                range-separator=""
                                start-placeholder=""
                                end-placeholder="">
                        </el-date-picker>
                    </el-form-item>


<!--                    <div v-if="DetailEditvisbleMessage.sid == null">-->
                    <el-form-item label="" prop="score" :rules="[{ required: true, message: '', trigger: 'blur' },{validator: 'regexp', pattern: /^([1-9][0-9]{0,1}|100)$/, message: '1-100', trigger: 'change,blur'}]">
                        <el-input  type="number" v-model="DetailEditvisbleMessage.score" placeholder="" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="teacherEstimate">
                        <el-input type="textarea" v-model="DetailEditvisbleMessage.teacherEstimate" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="tid" :rules="[{ required: true, message: '', trigger: 'change,blur' }]">
                        <el-select clearable @change="findTeacherMessage" v-model="DetailEditvisbleMessage.tid" filterable placeholder="">
                            <el-option
